:root {
  --mood-value: url('../../assets/images/mood-face-3.png');
}

#new-outing-page {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  #new-outing-form {
    background-color: white;
    display: flex;
    flex-direction: column;
    max-width: 32rem;
    width: 100%;
    padding: 1.2rem;
    border-radius: 0.5rem;
    border: 1px solid black;

    .MuiInputBase-root {
      margin-bottom: 1rem;
    }

    #mood-slider {
      span.MuiSlider-thumb {
        &::before {
          background-image: var(--mood-value);
          background-size: contain;
          background-color: white ;
        }
      }
    }


    // .MuiFormControl-root {
    //   background-color: white;
    //   margin: 0.1rem 1rem;
    // }
    // button {
    // }
  }
}